@import url(https://fonts.googleapis.com/css?family=Baloo+Chettan&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sawarabi+Mincho);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
                 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
                 sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
                 monospace;
}


.rainbow {
    /* 背景グラデーションを指定・幅を 200% にしておく */
    background: -webkit-gradient(linear, left top, right top, from(#f00), color-stop(14.28%, #f80), color-stop(28.56%, #dd0), color-stop(42.85%, #0d0), color-stop(57.14%, #0dd), color-stop(71.42%, #00f), color-stop(85.71%, #e0e), to(#f00)) 0% center / 200% auto;
    background: linear-gradient(to right, #f00 0%, #f80 14.28%, #dd0 28.56%, #0d0 42.85%, #0dd 57.14%, #00f 71.42%, #e0e 85.71%, #f00 100%) 0% center / 200% auto;

    /* 背景画像を文字でマスクする */
    background-clip: text;
    -webkit-background-clip: text;

    /* 文字色を透明にできればよく color: transparent でも color: rgba(0, 0, 0, 0) でも可 */
    text-fill-color: transparent;
    -webkit-text-fill-color: transparent;

    /* アニメーション指定 */
    -webkit-animation: example 4s linear infinite;
            animation: example 4s linear infinite;
}

/* 背景の横位置をズラす */
@-webkit-keyframes example {
    to { background-position-x: 200%; }
}
@keyframes example {
    to { background-position-x: 200%; }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

